import React, {Component} from 'react';
import {Table, Divider} from 'antd';
import {postAction} from "../../../../axios";
import ReactDOM from "react-dom";
// import {hostUrl} from "../../../../env";
import MainCard from '../../../../common/components/MainCard';
import LineChart from "./LineChart";


export default class QiShiFenXi extends Component {

    constructor(props){
        super(props);
        this.state = {
            tab: '1',
            chartIns: undefined,
            lineData: [
                // { name: '', '': 40, '': 20 }
            ],
            tableData: [
                // { key: '1', sortIndex: '1', campus: '', number: '2901', rate: '31%' }
            ],
            pagination: {
                current:1,
                pageSize:5,
                showQuickJumper: true,
                total: null,
                showTotal: (total) => {
                    return (
                        `${total}`
                    )
                }
            }
        }
        this.getData = this.getData.bind(this);
    }


    getData () {

        const {curCollege,curGrade, curMajor} = this.props.filterValue;
        postAction(
            "/bigdata/mentality/getGradeTrend",
            {
                collegeCode: curCollege,
                majorCode: curMajor,
                grade: curGrade,
                // time: curYear,
            }
        ).then(
            (res) => {
                if (res.success && res.obj.length > 0){
                    const pageInfo = res.obj;
                    let _chartData = [];
                    let _tableData = [];

                    pageInfo.forEach(
                        (item, index) => {

                            _chartData.push({
                                month: item.semesterYear + "",
                                "": Number(item.alarmLevelRed),
                                "": Number(item.alarmLevelOrange),
                                "": Number(item.alarmLevelYellow),
                                "": Number(item.alarmLevelBlue),
                            })

                            _tableData.push({
                                key: (index + 1),
                                sortIndex: (index + 1),
                                year: item.semesterYear,
                                red: item.alarmLevelRed,
                                orange: item.alarmLevelOrange,
                                yellow: item.alarmLevelYellow,
                                blue: item.alarmLevelBlue,
                            })
                        }
                    )
                    this.setState({
                        lineData: _chartData,
                        tableData: _tableData,
                        pagination:{
                            ...this.state.pagination,
                            total:pageInfo.length,
                        }
                    })
                } else {
                    this.setState({
                        lineData: [],
                        tableData: [],
                        pagination:{
                            ...this.state.pagination,
                            total:null,
                        }
                    })
                }
            }
        )
    }

    handleTableChange = (pagination) => {
        this.setState(
            () => {
                return {
                    pagination: {
                        current: pagination.current, // 
                        pageSize: pagination.pageSize, // 
                        total: pagination.total, // 
                        showTotal: (total) => `${total}`,
                        showQuickJumper: true // 
                    }
                }
            },
            () => {
                this.getData();
            }
        )
    }


    //
    // //
    // getDetail () {
    //     const {curYear,curCollege,curMajor,curGrade,curClass} = this.props.filterValue;
    //     window.location.href = `${hostUrl}/bigdata/student/exportStudentCampusDist?year=${curYear}&collegeCode=${curCollege}&majorCode=${curMajor}&grade=${curGrade}&classCode=${curClass}`;
    // }
    //
    changeTab = (e) => {
        this.setState({ tab: e.target.value }, () => {
            if (this.refs['table']) {
                const tableCon = ReactDOM.findDOMNode(this.refs['table'])
                const table = tableCon.querySelector('table')
                table.setAttribute('id', 'qushi-excel')
            }
            this.getData()
        })
    }

    render () {

        const columns = [
            {
                title:'',
                dataIndex: 'sortIndex',
                width: '10%',
            },{
                title: '',
                dataIndex: 'year',
            },{
                title: '',
                dataIndex: 'red',
            },{
                title: '',
                dataIndex: 'orange',
            },{
                title: '',
                dataIndex: 'yellow',
            },{
                title: '',
                dataIndex: 'blue',
            }
        ];

        return (
            <MainCard
                title={<div>
                    <div className="common-title">
                    <div style={{fontSize:12,color:"rgba(0, 0, 0, 0.45)",fontWeight:400}}>  {this.props.filterName.curCollegeName}
                        <Divider type="vertical" />{this.props.filterName.curMajorName}
                        <Divider type="vertical" />{this.props.filterName.curGradeName}
                    </div>
                    </div>
                </div>}
                tab={this.state.tab}
                changeTab={this.changeTab}
                downloadChart={{
                    chartIns: this.state.chartIns,
                    title:""
                }}
                // detailCallback={this.getDetail.bind(this)}
                tableToExcel={{
                    table: "qushi-excel",
                    filename: "execl"
                }}
            >
                {this.state.tab === '1' ? (
                    <LineChart
                        height={this.props.height - 100}
                        data={this.state.lineData}
                        padding={[10,40,60,60]}
                        onFirstLoad={(chartIns) => {
                            this.setState({ chartIns })
                        }}
                        // colors={['#1890ff','#2FC25B']}
                    />
                ) : (

                    <React.Fragment>
                        <Table
                            bodyStyle={{height:300}}
                            dataSource={this.state.tableData}
                            columns={columns}
                            bordered
                            size="middle"
                            pagination={this.state.pagination}
                            onChange={this.handleTableChange}
                        />

                        <Table ref='table' columns={columns} dataSource={this.state.tableData}  pagination={false} style={{display:'none'}} />
                    </React.Fragment>
                )}
            </MainCard>
        )
    }

    componentDidMount () {
        this.getData()
    }

    componentWillReceiveProps (nextProps) {
        if (JSON.stringify(nextProps) !== JSON.stringify(this.props)) {
            setTimeout(() => {
                this.getData()
            }, 50)
        }
    }
}


